import { Button, Stack } from "@chakra-ui/react";

import TransactionForm from "./modal/TransactionsForm";

const TransactionsBar = () => {
  return (
    <>
      <Stack spacing="2" direction="row" align="center">
        <TransactionForm
          title=" "
          button=" "
          isSpend
        />
        <TransactionForm
          title=" "
          button=" "
          isSpend={false}
        />
      </Stack>
    </>
  );
};

export default TransactionsBar;
